import Typography from 'typography'
import gray from 'gray-percentage'

const typography = new Typography({
  baseFontSize: "24px",
  overrideStyles: ({ adjustFontSizeTo, scale, rhythm }, options) => ({
    '@media screen and (max-width: 992px)': {
      html: {
        fontSize: '16px',
      },
    },
  }),
  bodyColor: gray(30),
  headerColor: gray(20),
  googleFonts: [
    {
      name: "Pontano Sans",
      styles: [
        "400"
      ]
    },
    {
      name: "Lato",
      styles: [
        "Regular",
        "Regular Italic",
        "Bold",
        "Bold Italic"
      ]
    }
  ],
  headerFontFamily: [
    "Pontano Sans",
    "sans-serif"
  ],
  headerWeight: "400",
  bodyFontFamily: [
    "Lato",
    "sans-serif"
  ]
});

export default typography;


